                    </div>
                    <div v-else class="modal-body"></div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" :disabled="sucessoEditar || !objetoModal" @click.prevent="deletar()"><i class="mdi mdi-trash-can font-size-18"></i> Desativar </button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                    </div>
