        <div class="modal fade" id="modal-editar-condicao" tabindex="-1" aria-labelledby="modal-editar-title" aria-hidden="true" style="display: none;">
            <div class="modal-dialog modal-lg modal-dialog-scrollable">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-editarTitle"> Cadastro de Condição de Entrada</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    <form class="modal-body" v-if="objetoModal && condicaoModal" @submit.prevent="salvarCondicao()">
                        <div class="row">
