                                <h5>Tipo: {{objetoModal.descricao}}</h5>

                            </div>
                           <label class="col-sm-3 col-form-label">Descrição</label>
                            <div class="col-sm-9">
                                <input-componente
                                    v-model="condicaoModal.descricao"
