                            <div class="col-sm-9">
                                <container-componente :validator="$v.condicaoModal.ativo">
                                    <div class="custom-control custom-checkbox mb-3">
                                        <input type="checkbox" class="custom-control-input" id="customCheckativo2" v-model="condicaoModal.ativo">
