                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-delete-condicaoTitle">  Confirmação de Desativação de Condição de Entrada</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    <div class="modal-body" v-if="objetoModal && condicaoModal">
                        <p v-if="!sucessoEditar">Tem certeza que deseja a condição de Entrada <strong>{{condicaoModal.descricao}}</strong> o Tipo <strong> {{objetoModal.descricao}} </strong> ?</p>
                        
                        <div v-if="sucessoEditar" class="text-center" >
                            <div class="swal2-icon swal2-success swal2-icon-show" style="display: flex;">
                                <div class="swal2-success-circular-line-left" style="background-color: rgb(255, 255, 255);"></div>
                                <span class="swal2-success-line-tip"></span> <span class="swal2-success-line-long"></span>
                                <div class="swal2-success-ring"></div> <div class="swal2-success-fix" style="background-color: rgb(255, 255, 255);"></div>
                                <div class="swal2-success-circular-line-right" style="background-color: rgb(255, 255, 255);"></div>
                            </div>
                            <p class="success text-center font-size-18">{{sucessoEditar}}</p>
                            
                        </div>
                        <mensagem-componente titulo="Erro!" v-if="erroEditar" :erros="erroEditar" @fechar="erroEditar = null"></mensagem-componente>
                    </div>
                    <div v-else class="modal-body"></div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" :disabled="sucessoEditar || !condicaoModal" @click.prevent="deletarCondicao()"><i class="mdi mdi-trash-can font-size-18"></i> Desativar </button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                    </div>
