<script>
import { required,  maxLength, } from "vuelidate/lib/validators";
import { TipoItemOsService } from '../../../services'

const tipoItemOsService = new TipoItemOsService();

export default {
    data() {
        return {
            dados: [],
            filtro:{
                ordenar: 'descricao',
                ordem: 'asc',
                page: 1,
                per_page: 100,
                texto: '',
            },
            paginacao: {
                current_page: 0,
                // total: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 100
            },

            debounce: null,
            objetoModal: null,
            condicaoModal: null,
            sucessoEditar: null,
            erroEditar: null,
           
        };
    },
    validations() {
        return {
            objetoModal: {
                descricao: { required, maxLength:maxLength(50)},
                ativo: { required },
            },
            condicaoModal: {
                descricao: { required, maxLength:maxLength(50)},
                ativo: { required },
            },
        }
        
    },
    mounted() {
        this.buscar();
    },
    methods: {

        abrirModalEditar:function(obj){
            
            this.sucessoEditar = null;
            this.erroEditar = null;
            if(obj != null){
                this.objetoModal = {...obj};
            }else{
                this.objetoModal = {ativo: true, descricao: ''};
            }
        },

        salvar: function(){
            this.sucessoEditar = null;
            this.erroEditar = null;

            this.$v.objetoModal.$touch();
            if (this.$v.objetoModal.$invalid){
                this.erroEditar = this.$v.objetoModal;
                return false;
            }

            let req = this.objetoModal.id ? tipoItemOsService.atualizar(this.objetoModal) : tipoItemOsService.criar(this.objetoModal);

            req.then( response => {
                let obj = response.data;
                obj.mostrar_condicoes = false;
                let i = this.dados.map(x => x.id).indexOf(obj.id);
                if(i > -1)
                    this.dados.splice(i,1,obj);
                else
                    this.dados.push(obj);
                
                this.sucessoEditar = 'Tipo salvo com sucesso!';
            }).catch( erro => this.erroEditar = erro)            
        },

        deletar: function(){
            this.sucessoEditar = null;
            this.erroEditar = null;

         tipoItemOsService.deletar(this.objetoModal.id)
            .then( response => {
                let i = this.dados.map(x => x.id).indexOf(this.objetoModal.id);
                this.dados[i].ativo = false;
                this.sucessoEditar = this.objetoModal.descricao + ': Desativado com sucesso!';
            }).catch( erro => this.erroEditar = erro)            
        },

        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },
        
        buscar:function(){
            tipoItemOsService.listar(this.filtro).then(
                response => {
                    this.$data.dados = response.data.data.map(x => {
                        x.mostrar_condicoes = false;
                        return x;
                    });
            })
        },

        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscar();
        },


        abrirModalEditarCondicao:function(tipo, obj){

            this.sucessoEditar = null;
            this.erroEditar = null;
            this.objetoModal = {...tipo}
            if(obj != null){
                this.condicaoModal = {...obj};
            }else{
                this.condicaoModal = {ativo: true, descricao: ''};
            }
        },

        salvarCondicao: function(){
            this.sucessoEditar = null;
            this.erroEditar = null;

            this.$v.condicaoModal.$touch();

            if (this.$v.condicaoModal.$invalid){
                // this.erroEditarTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erroEditar = this.$v.condicaoModal;
                return false;
            }

           tipoItemOsService.salvarCondicao(this.objetoModal.id, this.condicaoModal).then( response => {
                let obj = response.data;

                let i = this.objetoModal.condicoes.map(x => x.id).indexOf(obj.id);
                if(i > -1)
                    this.objetoModal.condicoes.splice(i,1,obj);
                else
                    this.objetoModal.condicoes.push(obj);


                i = this.dados.map(x => x.id).indexOf(this.objetoModal.id);
                this.dados.splice(i,1,this.objetoModal);
                
                
                this.sucessoEditar = 'Condição salva com sucesso!';
            }).catch( erro => this.erroEditar = erro)
        },

        deletarCondicao: function(){
            this.sucessoEditar = null;
            this.erroEditar = null;

            this.condicaoModal.ativo = false;

            tipoItemOsService.salvarCondicao(this.objetoModal.id, this.condicaoModal).then( response => {
                let obj = response.data;

                let i = this.objetoModal.condicoes.map(x => x.id).indexOf(obj.id);
                if(i > -1)
                    this.objetoModal.condicoes.splice(i,1,obj);
                else
                    this.objetoModal.condicoes.push(obj);


                i = this.dados.map(x => x.id).indexOf(this.objetoModal.id);
                this.dados.splice(i,1,this.objetoModal);
                
                
                this.sucessoEditar = 'Condição Desativada com sucesso!';
            }).catch( erro => this.erroEditar = erro)
        },
       
  
    }
};
</script>
<style scoped>
    tr:hover{
        background-color: #eff2f7 ;
    }
</style>
